.gallery__wraper {
    position: relative;
    max-width: 100%;
    height: 130px;
    margin: 0 auto;

    @media screen and (min-width: 480px) {
        max-width: 280px;
        height: 130px;
    }

    @media screen and (min-width: 768px) {
        max-width: 704px;
        height: 327px;
    }

    @media screen and (min-width: 1200px) {
        max-width: 1013px;
        height: 422px;
    }
}

.gallery__container {
margin-top: 32px;
        margin-bottom: -98px;

    @media screen and (min-width: 767px) {
        margin-top: 60px;
            margin-bottom: -183px;
            }

    @media screen and (min-width: 1199px) {
        margin-top: 80px;
            margin-bottom: -324px;
            }
}

@keyframes gallery {
    0% {
            opacity: 1;
        }
            25%   {opacity: 1;
        }
            33% {
            opacity: 0;
        }          
        92% {
            opacity: 0;
        }                100% {
                    opacity: 1;
                }
   
}

.gallery__item {
    border-radius: 24px;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;


    animation-name: gallery;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;
        animation-duration: 9s;
    
        &:nth-of-type(1) {
            animation-delay: 6s;
        }
    
        &:nth-of-type(2) {
            animation-delay: 3s;
        }
    
        &:nth-of-type(3) {
            animation-delay: 0;
        } 
        
    
}

