.products {
  outline-offset: -2px;
  padding-top: 60px;
  padding-bottom: 60px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  background-image: url('../images/products/bcg-mob.jpg');

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/products/bcg-mob@2x.jpg');
  }

  @media screen and (min-width: 481px) {
    background-image: url('../images/products/bcg-tab.jpg');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/products/bcg-tab@2x.jpg');
    }
  }

  @media screen and (min-width: 769px) {
    background-image: url('../images/products/bcg-desk.jpg');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/products/bcg-desk@2x.jpg');
    }
  }
}

.products__label {
  font-weight: 800;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  text-transform: uppercase;
  color: var(--second-text-color);
  margin-bottom: 12px;

  @media screen and (min-width: 768px) {
    font-size: 22px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.23;
    letter-spacing: 0.04em;
  }
}

.products__title {
  font-family: 'Seymour One';
  font-weight: 400;
  font-size: 30px;
  line-height: 1.27;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--second-text-color);
  margin-bottom: 148px;

  @media screen and (min-width: 768px) {
    font-size: 34px;
    margin-bottom: 200px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 36px;
    line-height: 1.25;
    letter-spacing: 0.06em;
    margin-bottom: 303px;
  }
}

.products__list {
  @media screen and (min-width: 768px) {
    display: flex;
    gap: 16px;
  }

  @media screen and (min-width: 1200px) {
    gap: 29px;
  }
}

.products__item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 123px 40px 16px;
  min-height: 420px;
  border-radius: 24px;

  @media screen and (min-width: 768px) {
    padding: 108px 26px 12px;
    width: calc((100% - 32px) / 3);
    min-height: 428px;
  }

  @media screen and (min-width: 1200px) {
    padding: 165px 64px 60px;
    width: calc((100% - 58px) / 3);
    min-height: 536px;
  }
}

.products__item:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 168px;
  }
}

.products__item:first-child {
  background-color: var(--lite-green);
}

.products__item:nth-child(2) {
  background-color: var(--lite-yellow);
}

.products__item:last-child {
  background-color: var(--lite-orange);
}

.products__item::before {
  content: ' ';
  position: absolute;
  left: 50%;
  top: -108px;
  transform: translateX(-50%);
  background-position: 0 0;
  border-radius: 50%;
  width: 200px;
  height: 211px;

  @media screen and (min-width: 768px) {
    top: -120px;
  }

  @media screen and (min-width: 1200px) {
    top: -183px;
    width: 302px;
    height: 318px;
  }
}

.products__item:first-child::before {
  content: ' ';
  background: url('../images/products/img1-mob200.png'),
    linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)),
    #d9d9d9;
  box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
  background-repeat: no-repeat;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background: url('../images/products/img1-mob200@2x.png'),
      linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)),
      #d9d9d9;
    background-repeat: no-repeat;
    background-size: contain;
  }

  @media screen and (min-width: 1200px) {
    background: url('../images/products/img1-desk302.png'),
      linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)),
      #d9d9d9;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background: url('../images/products/img1-desk302@2x.png'),
        linear-gradient(
          0deg,
          rgba(156, 190, 143, 0.7),
          rgba(156, 190, 143, 0.7)
        ),
        #d9d9d9;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.products__item:nth-child(2)::before {
  content: ' ';
  background-size: cover;
  background-position: center;
  background: url('../images/products/img2-mob200.png'),
    linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)),
    #d9d9d9;
  box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
  background-repeat: no-repeat;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background: url('../images/products/img2-mob200@2x.png'),
      linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)),
      #d9d9d9;
    background-repeat: no-repeat;
    background-size: contain;
  }

  @media screen and (min-width: 1200px) {
    background: url('../images/products/img2-desk302.png'),
      linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)),
      #d9d9d9;
    box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background: url('../images/products/img2-desk302@2x.png'),
        linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)),
        #d9d9d9;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.products__item:last-child::before {
  content: ' ';
  background: url('../images/products/img3-mob200.png'),
    linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)),
    #d9d9d9;
  box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
  background-repeat: no-repeat;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background: url('../images/products/img3-mob200@2x.png'),
      linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)),
      #d9d9d9;
    background-repeat: no-repeat;
    background-size: contain;
  }

  @media screen and (min-width: 1200px) {
    background: url('../images/products/img3-desk302.png'),
      linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)),
      #d9d9d9;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background: url('../images/products/img3-desk302@2x.png'),
        linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)),
        #d9d9d9;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.products__name {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--second-text-color);
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 1.23;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.23;
    margin-bottom: 66px;
  }
}

.products__descr {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.78;
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--second-text-color);
  width: 200px;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    width: 172px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.75;
    width: 242px;
  }
}

.products__btn {
  background: none;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  padding: 6px 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 100ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 100ms cubic-bezier(0.4, 0, 0.2, 1);
}

.products__btn:hover,
.products__btn:focus {
  transform: scale(1.4);
  box-shadow: -7px -7px 20px 0px var(--lite-green), -4px -4px 5px 0px #fff9,
    7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001;
}

.products__btn--yellow:hover,
.products__btn--yellow:focus {
  box-shadow: -7px -7px 20px 0px var(--lite-yellow), -4px -4px 5px 0px #fff9,
    7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001;
}

.products__btn--red:hover,
.products__btn--red:focus {
  box-shadow: -7px -7px 20px 0px var(--lite-orange), -4px -4px 5px 0px #fff9,
    7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001;
}
